@import '../../styles/theme.scss';

.nameLog {
  width: 180px;
  height: 180px;
  background: $color-tertiary;
  font-size: 130px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $font-color-primary;
}
.nameCont {
  width: -webkit-fill-available;
  display: flex;
  justify-content: center;
  margin: 20px;
}
.detailCont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  background: #5878507a;
  border-radius: 30px;
  color: white;
  border: 2px solid #78b0a0;
  align-items: center;
  span {
    margin-bottom: 1rem;
    &:last-child {
      margin-bottom: 0px;
    }
  }
}
.buttonCont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  & > div {
    width: 5rem;
    margin-top: 2rem;
    &:first-child {
      div:first-child {
        background-color: red;
      }
    }
  }
}
