@import '../../styles/theme.scss';

.inputtransparent {
  background: none;
  border: none;
  margin: 0.5rem;
  color: $color-primary;
  width: -webkit-fill-available;
  &:focus {
    outline: none;
  }
  &::placeholder {
    color: $color-primary;
    opacity: 0.5;
  }
}
.container {
  background-color: white;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10rem;
  margin: 0.5rem;
  width: 70%;
  display: flex;
}
