@import '../../styles/theme.scss';

.container {
  background: #58785080;
  width: 10rem;
  height: 10rem;
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 30px;
  border: 2px solid #78b0a0;
  position: relative;
  margin: 0.5rem;

  img {
    width: 100px;
  }
}
.heading {
  color: $font-color-primary;
  font-size: $font-size-sub-title;
}
.price {
  color: $font-color-primary;
  font-size: $font-size-sub-title;
}
.favIcon {
  position: absolute;
  right: 10px;
  bottom: 7px;
  img {
    width: 24px;
  }
}
