@import '../../styles/theme.scss';
.graphContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 15rem);
  span {
    color: $color-primary;
    padding: 3rem;
    text-align: center;
  }
}
.SearchCont {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  .addProd {
    background: #fff;
    padding: 10px;
    border-radius: 10px;
    border-color: #c0c0c0;
    border-width: 1px;
    border-style: solid;
    margin: 0.5rem;
  }
  .deleteProd {
    color: red;
  }
}
.feedAddCont {
  display: flex;
  justify-content: center;
}
