@import '../../styles/theme.scss';

.container {
  background: #803a026e;
  min-width: 20rem;
  width: -webkit-fill-available;
  height: 10rem;
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 30px;
  border: 2px solid #b58c52;
  position: relative;
  margin-bottom: 0.5rem;

  img {
    width: 100px;
  }
}
.content {
  color: $font-color-primary;
  font-size: $font-size-sub-title;
  overflow-wrap: anywhere;
  text-overflow: ellipsis;
  height: 3rem;
  overflow: hidden;
}
.date {
  color: $font-color-primary;
  position: absolute;
  right: 0px;
  top: 0px;
  font-size: 12px;
  position: absolute;
  right: 12px;
  top: 12px;
}
