@import '../../styles/theme.scss';
.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  & > img:first-child {
    width: 40%;
    margin-bottom: 2rem;
  }
}

.loginContainer {
  padding: 1rem;
  background: #58785038;
  border-radius: 31px;
  width: 75%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.orCont {
  padding: 1.5rem;
  color: $color-primary;
}

@mixin loginButton {
  display: flex;
  justify-content: space-around;
  width: 70%;
  font-size: $font-size-normal;
  align-items: center;
  border-radius: 50rem;
  padding: 10px;
  margin-bottom: 1rem;
  img {
    width: 24px;
  }
}

.googleSign {
  @include loginButton();
  background: $font-color-primary;
  color: $color-primary;
}
.fbSign {
  @include loginButton();
  background: #1877f2;
  color: $font-color-primary;
}

.backgroundImage {
  position: fixed;
  width: 582px;
  height: 705px;
  left: -33px;
  bottom: -100px;
  mix-blend-mode: normal;
  opacity: 0.3;
  z-index: -1;
}
