@import '../../styles/theme.scss';

.bottomNavContainer {
  width: 100%;
  position: fixed;
  bottom: 0px;
  background: $color-primary;
  height: 50px;
  left: 0px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0px -2px 10px 5px rgba(0, 0, 0, 0.24);
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 12px;
}
.itemHolder {
  display: flex;
  flex-direction: column;
  color: $font-color-primary;
  justify-content: center;
  align-items: center;
  z-index: 9;
  img {
    width: 24px;
  }
}
.highlighter {
  background: $color-tertiary;
  position: absolute;
  height: 100%;
  border-radius: 10px;
}
