@import url('https://fonts.googleapis.com/css2?family=Finger+Paint&display=swap');
@import './global.scss';

$color-primary: #587850;
$color-secondary: #709078;
$color-tertiary: #b58c52;
$color-accent: #78b0a0;
$color-background: #e9dea2;
$font-color-primary: #ffff;
$font-size-title: 45px;
$font-size-sub-title: 20px;
$font-size-normal: 14px;
$font_family-head: 'Finger Paint', cursive;
$font-stack: 'Arial', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
  'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
$outSetShadow: 0px -2px 10px 5px rgba(0, 0, 0, 0.24);
